/* /// Tool Tip hover /// */
.password-info-icon {
  /* hack to fill transparent back ground color */
  background: white;
  border-radius: 50%;
  height: 1em;
  width: 1em;
}

.tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgba(0, 0, 0, 0.75);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  font-size: 0.9rem;
  line-height: 1.3em;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.password-info-icon:hover .tooltiptext {
  visibility: visible;
  left: 50%;
}
/* /// End of Tool Tip hover CSS /// */

